@import url(https://fonts.googleapis.com/css?family=Fira+Sans:400,500,600,700,800);
* {
  box-sizing: border-box;
}
body {
  background: #1C8EF9 !important;
  min-height: 100vh;
  display: flex;
  font-weight: 400;
  font-family: 'Fira Sans', sans-serif;
}

h1,h2,h3,h4,h5,h6,label,span {
  font-weight: 500;
  font-family: 'Fira Sans', sans-serif;
}

body, html, .App, #root, .auth-wrapper {
  width: 100%;
  height: 98%;
}

.auth-wrapper h1 {
  text-align: center;
  padding-top: 2rem;
  color: white;
  text-decoration: underline;
}

.navbar-light {
  background-color: #ffffff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
}

.auth-wrapper {
  display: flex;
  justify-content: center;
  flex-direction: column;
  text-align: left;
}

.auth-inner {
  width: 450px;
  margin: auto;
  background: #ffffff;
  box-shadow: 0px 14px 80px rgba(34, 35, 58, 0.2);
  padding: 40px 55px 45px 55px;
  border-radius: 15px;
  transition: all .3s;
}

.auth-wrapper .form-control:focus {
  border-color: #167bff;
  box-shadow: none;
}

.auth-wrapper h3 {
  text-align: center;
  margin: 0;
  line-height: 1;
  padding-bottom: 20px;
}

.custom-control-label {
  font-weight: 400;
}

.forgot-password,
.forgot-password a {
  text-align: right;
  font-size: 13px;
  padding-top: 10px;
  color: #7f7d7d;
  margin: 0;
}

.forgot-password a {
  color: #167bff;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.dialogbox {
    display: flex;
    flex-flow: column;
    align-items: center;
}

.dialogbox img{
    object-fit: cover;
}

.Home {
    display: flex;
    flex-wrap: wrap;
}

.PostItem h5, .PostItem p {
    margin: 0;
}

.PostContent {
    padding: 8px;
}

.PostMetadata {
    padding: 0 8px;
    padding-bottom: 8px;
}

.PostItem {
    flex: 1 0 21%;
    margin: 1rem;
    background-color: white;
    text-align: left;
    border-radius: 8px;
}

.PostItem img {
    width: 100%;
    height: 200px;
    border-top-left-radius: 8px;
    border-top-right-radius: 8px;
    cursor: pointer;
}

.PostItem .content {
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    height: 40px;
}

.PostPage {
    width: 60%;
    margin: 2rem auto;
    background: white;
    padding: 1rem;
    text-align: left;
}

.PostPage h1, .PostPage p{
    margin-top: 1rem;
}

.PostPage p {
    font-size: 1.2rem;
}


.PostPage img {
    width: 100%;
    height: 300px;
    object-fit: cover;
}

.DashPosts {
    width: 100%;
}

.MyList {
    width: 100%;
}

.MyItem {
    background: #eee;
    padding: 8px;
    margin: 8px 0;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
}

.MyItem p {
    margin: 0;
}

.Actions {
    display: flex;
    align-items: center;
}

.Actions svg {
    margin-right: 8px;
}


.Actions svg:hover{
    cursor: pointer;
    color: #1C8EF9;
}
